export default {
	data() {
		return {
			imgurl: this.util.uploaddata.cdnurl,
			defaultImgSrc: '../../../static/image/yangsongyan/release02/01.png',
			tabIndex: 0,
			pageSize: 0,
			loadingText: '加载更多...',
			tabBars: [{
				name: '抖音点评转',
				id: 'dou'
			}, {
				name: '抖音点评',
				id: 'friends'
			}, {
				name: '朋友圈',
				id: 'guanzhu'
			}],
			data: []
		}
	},
	onLoad: function(options) {
		setTimeout(function() {
			// console.log('start pulldown');
		}, 1000);
		uni.startPullDownRefresh();
		let params = {
			"task_type_id": 1
		};
		let url = "/api/task";
		this.util.request(url, "GET", params, (res) => {
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.data = res.data.data;
					// console.log(JSON.stringify(this.data));
				} else {
					this.util.showWindow("注册失败");
				}
			} else {
				this.util.showWindow("注册请求失败");
			}
		});
	},
	// 下拉方法
	onPullDownRefresh() {
		// console.log('下拉刷新');
		this.pageSize = 0;
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
		// this.loadingText = "下拉刷新"
	},
	// xia划方法
	onReachBottom: function() {
		// console.log("下划")
		this.pageSize++;
		// this.loadingText = "没有更多了"
	},
	methods: {
		taskDetail(id) {
			uni.navigateTo({
				url:"../task-detail/task-detail?id="+id
			})
		},
		// 切换触发
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index
			let params = {
				"task_type_id": this.tabIndex + 1
			};
			let url = "/api/task";
			this.util.request(url, "POST", params, (res) => {
				if (res.statusCode == 200) {
					if (res.data.code == 1) {
						this.data = res.data.data;
					} else {
						this.util.showWindow("请求失败");
					}
				} else {
					this.util.showWindow("请求失败");
				}
			});

		}
	},
	mounted() {}
}
